* {
  box-sizing: border-box;
}

html,
body {
  color: $black-off;
  font-size: 16px;
  font-family: 'ff-enzo-web', sans-serif;
}

body {
  padding-top: 74px;
}

img {
  max-width: 100%;
  height: auto;
}

.break-md-only {
  display: none;
}

button {

  -webkit-appearance: none;

  &.btn {

    padding: 0.75rem;
    color: $white;
    background: $yellow;
    border: 0px;
    outline: 0px;
    border-radius: 0.25em;
    text-transform: uppercase;
    font-weight: bold;

    &:hover {
      background: darken($yellow, 5%);
      cursor: pointer;
    }

    &:active {
      background: darken($yellow, 10%);
    }

  }

}

.main-menu-toggle,
.close-main-menu {
  display: none;
}

header {

  z-index: 10000;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  padding: 1.5rem 0px;
  background: $white;
  border-bottom: 2px solid $grey-light;

  .grid-wrapper {
    position: relative;
  }

  .logo-main {

    display: block;

    &.logo-cvln {
      display: none;
    }

    img {
      display: block;
      height: 1.5rem;
    }

  }

}

.page-header {

  position: relative;
  height: 400px;
  background: {
    size: cover;
    position: center center;
    repeat: no-repeat;
  }
  border-bottom: 0.25rem solid $red-orange;

  &.header-covered:after {
    content: ' ';
    z-index: 1;
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    background: $turquoise-darkest;
    opacity: 0.5;
  }

  .page-header-meta {
    z-index: 2;
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 0px;
  }

  .title-boxed {
    margin: 0px;
    padding: 0.375em 0.5em;
    transform: translate(0px, 50%);
    color: $red-orange;
    background: $white;
    border-color: $red-orange;
    font-size: 1.75rem;
  }

}

a {

  &.arrow-link {

    position: relative;
    display: block;
    line-height: 1;

    &:after {
      content: ' ';
      position: absolute;
      top: 0px;
      right: 0px;
      width: 1em;
      height: 1em;
      background: {
        image: url('../images/chevron-with-circle-right.svg');
        size: contain;
        repeat: no-repeat;
      }
    }

    &-centered:after {
      top: 50%;
      transform: translate(0px, -50%);
    }

  }

}

.nav-main-menu {

  position: absolute;
  top: 0.25rem;
  right: 0px;

  ul {

    list-style-type: none;
    margin: 0px;
    padding: 0px;

    li {

      display: inline-block;
      vertical-align: middle;

      &:not(:last-child) {
        margin-right: 1rem;
      }

      &:nth-child(1) a:before {
        content: '01';
        color: $green-dark;
      }

      &:nth-child(2) a:before {
        content: '02';
        color: $turquoise;
      }

      &:nth-child(3) a:before {
        content: '03';
        color: $red;
      }

      &:nth-child(4) a:before {
        content: '04';
        color: $green;
      }

      &:nth-child(5) a:before {
        content: '05';
        color: $turquoise;
      }

      &:nth-child(6) a:before {
        content: '06';
        color: $green-dark;
      }

    }

  }

  a {

    color: $black;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;

    &:before {
      margin-right: 0.25rem;
    }

  }

}

.grid-wrapper {
  margin: 0px auto;
  width: 60rem;
}

.title-boxed-wrap {
  z-index: 1;
  position: relative;
  text-align: center;
}

.title-boxed {
  display: inline-block;
  padding: 0.45rem;
  margin-bottom: 2rem;
  border: 0.125em solid $black;
  line-height: 1;
  letter-spacing: 0.5px;
  font-weight: bold;
  text-transform: uppercase;
}

.home-header {

  overflow: hidden;
  position: relative;
  height: 52rem;
  border-bottom: 0.375rem solid $red;

  .cvln-bw {
    z-index: 1;
    position: absolute;
    bottom: -0.75rem;
    left: 50%;
    transform: translate(-50%, 0px);
    height: 13rem;
  }

  video {
    z-index: 0;
    position: absolute;
    top: 50%;
    left: 0px;
    right: 0px;
    transform: translate(0px, -50%);
    width: 100%;
  }

}

.preview-card {

  &-tight {

    margin-right: 2%;
    width: 49%;

    &:nth-child(2n) {
      margin-right: 0px;
    }

  }

  a {

    display: block;
    position: relative;
    height: 350px;
    background: {
      size: cover;
      position: center center;
      repeat: no-repeat;
    }

    &:after {
      z-index: 1;
      position: absolute;
      content: ' ';
      left: 0px;
      right: 0px;
      bottom: 0px;
      height: 56%;
      background: linear-gradient(to bottom, transparent 0%, $black 100%);
    }

  }

  .preview-card-meta {
    z-index: 2;
    position: absolute;
    bottom: 1.75rem;
    left: 1.75rem;
    right: 1.75rem;
  }

  .title-boxed {
    margin: 0px 0px 0.5rem;
    color: $yellow;
    font-size: 0.875rem;
    border-color: $yellow;
    border-width: 0.125rem;
  }

  &-title {
    width: 235px;
    color: $white;
    font-size: 1.375rem;
    line-height: 1.3;
    font-weight: bold;
    text-decoration: none;
  }

}

.home-preview {

  padding: 2.5rem;
  padding-bottom: 1rem;
  color: $white;

  &:nth-child(1) {

    background: $turquoise-darkest;

    a:hover {
      background: $turquoise-darkest-hover;
    }

  }

  &:nth-child(2) {

    background: $turquoise;

    a:hover {
      background: $turquoise-hover;
    }

  }

  &:nth-child(3) {

    background: $red-orange;

    a:hover {
      background: $red-orange-hover;
    }

  }

  .title-boxed {
    margin: 0px;
    border-color: $white;
    border-width: 0.125rem;
    font-size: 0.875rem;
    letter-spacing: 1px;
  }

  h2 {
    padding-right: 1rem;
    font-size: 1.375rem;
    line-height: 1.35;
  }

  p {
    margin-bottom: 1em;
    min-height: 16rem;
    font-size: 1.125rem;
    line-height: 1.2;
    letter-spacing: 0.5px;
  }

  a {

    margin-left: -2.5rem;
    margin-right: -2.5rem;
    padding: 1rem 2.5rem;
    color: $white;
    text-decoration: none;
    font-size: 1.25rem;

    &:after {
      right: 2.5rem;
    }

  }

}

.case-header {

  overflow: hidden;
  position: relative;
  padding: 60px 0px 0px;

  video {
    z-index: 0;
    position: absolute;
    top: 50%;
    left: 0px;
    right: 0px;
    transform: translate(0px, -50%);
    width: 100%;
  }

  .case-header-content {
    z-index: 1;
    position: relative;
    padding: 30rem 0px;
    text-align: center;
  }

  .title-boxed {
    color: $white;
    border-color: $white;
    font-size: 1rem;
  }

  h1 {
    margin: 0px;
    color: $white;
  }

  .accent {

    z-index: 2;
    position: absolute;
    width: 100px;
    height: 100px;
    opacity: 0.7;

    &.yellow {
      background: $yellow;
    }

    &.red {
      background: $red;
    }

    &.turquoise {
      background: $turquoise;
    }

    &.right-top {
      z-index: 2;
      left: 50%;
      top: 150px;
      transform: translate(400px, 0px);
    }

    &.right-bottom {
      left: 50%;
      top: 215px;
      transform: translate(325px, 0px);
    }

    &.left-top {
      z-index: 2;
      left: 50%;
      top: 425px;
      transform: translate(-400px, 0px);
    }

    &.left-bottom {
      left: 50%;
      top: 350px;
      transform: translate(-475px, 0px);
    }

  }

}

%main-text {

  font-size: 1.35rem;
  letter-spacing: 0.5px;
  line-height: 1.5;

  a {
    color: $black;
    font-weight: bold;
  }

}

.content-main {

  padding: 4rem 0px;

  p {
    @extend %main-text;
  }

  h2 {
    color: $red-orange;
  }

}

.home-cases {

  padding: 2rem 0px;
  background: $grey-lightest;

  .title-boxed {
    color: $red;
    border-color: $red;
  }

  img {
    margin-bottom: 1rem;
  }

  .arrow-link {

    color: $turquoise-darkest;
    font-size: 1.25rem;
    line-height: 1.2;
    font-weight: 600;
    text-decoration: none;

    &:after {
      background-image: url('../images/chevron-with-circle-right-turquoise-darkest.svg');
    }

  }

}

.home-map {

  padding: 4rem 0px;

  .title-boxed {
    color: $red;
    border-color: $red;
  }

  p {
    @extend %main-text;
  }

  .google-map {
    height: 32rem;
  }

  .mobile-map-launch,
  .map-mobile-overlay {
    display: none;
  }

  .map-infobubble {

    padding: 0.5rem;
    max-width: 10rem;
    color: $turquoise-darkest;
    font-size: 1rem;
    text-align: center;

    .point-title {
      letter-spacing: 0.5px;
      font-weight: bold;
    }

  }

}

.snacks {
  margin-top: 3.75rem;
}

.snacks-item {

  display: none;

  &.active {
    display: block;
  }

  &,
  h2 {
    color: $white;
  }

  h2 {

    margin-top: 0px;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 0.125rem solid $white;
    line-height: 1.3;

    .subtitle {
      font-weight: normal;
    }

  }

  &-content {
    padding: 2.5rem;
    width: 530px;
    background: transparentize($red-orange, 0.1);

  }

  p {
    margin: 0px;
    font-size: 1rem;
    line-height: 1.5;
  }

  .ways-we-connected {

    p {
      margin: 0px 0px 0.5rem;
      font-size: 0.875rem;
    }

    h3 {
      margin: 0px 0px 0.5rem;
      font-size: 0.875rem;
      text-transform: uppercase;
    }

  }

}

.snacks-nav {

  margin-top: 2.5rem;
  padding-bottom: 4rem;
  text-align: center;

  .snacks-nav-line {

    position: relative;
    display: inline-block;

    &:before {
      content: ' ';
      z-index: 0;
      position: absolute;
      top: 50%;
      left: 0.9375rem;
      right: 0.9375rem;
      transform: translate(-0px, -50%);
      height: 0.25rem;
      background: $turquoise-darkest;
    }

  }

  .snacks-point {

    z-index: 1;
    position: relative;
    display: inline-block;

    &:not(:last-child) {
      margin-right: 6rem;
    }

    &.active,
    &:hover {

      cursor: pointer;

      .snacks-dot {
        background: $red-orange;
      }

      .snacks-logo-wrap {
        opacity: 1;
      }

    }

    .snacks-dot {
      width: 1.875rem;
      height: 1.875rem;
      background: $white;
      border: 0.25rem solid $turquoise-darkest;
      border-radius: 1.875rem;
      transition: $transition;
    }

    .snacks-logo-wrap {

      position: absolute;
      top: 1rem;
      left: 50%;
      transform: translate(-50%, 0px);
      padding-top: 3rem;
      opacity: 0.15;
      transition: $transition;

      img {
        transform: translate(0px, -50%);
        width: auto;
        max-width: 6rem;
        max-height: 3rem;
      }

    }
  }

}

.case-main-content {

  padding: 6rem 0px 0px;
  color: $black;
  background: $white;

  .title-boxed-wrap {
    margin-bottom: 4rem;
  }

  h2.title-boxed {
    margin: 0px;
    color: $turquoise;
    background: $white;
    border-color: $turquoise;
    font-size: 1.75rem;
  }

}

.timeline-solid {

  position: relative;

  &.timeline-red {

    background: $red;

    &:after {
      content: ' ';
      position: absolute;
      bottom: 0px;
      left: 0px;
      right: 0px;
      height: 4rem;
      background: $red;
    }

  }

  .title-boxed {
    transform: translate(0px, -50%);
  }

}

.case-timeline {

  $circle-dimension: 5.5rem;
  $timeline-border-width: 3px;

  position: relative;

  .timeline-circle {

    z-index: 2;
    position: relative;
    display: inline-block;
    width: $circle-dimension;
    height: $circle-dimension;
    border-width: $timeline-border-width;
    border-style: solid;
    border-radius: 50%;

    img {

      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: $circle-dimension - 2rem;

      &.tall {
        height: $circle-dimension - 3rem;
      }

      &.vtall {
        height: $circle-dimension - 3.5rem;
      }

    }

    &.timeline-circle-main-logo {

      position: absolute;
      top: 0px;
      left: 50%;
      transform: translate(-50%, -50%);
      background: $white;
      border-color: $turquoise;

      .case-clearchoice & {
        border-color: $blue-off;
      }

    }

  }

  .timeline-point {

    z-index: 0;
    position: relative;
    padding: 1rem 2rem;
    margin-top: -1/2 * $circle-dimension;
    width: 50%;
    text-align: left;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }

    &:before {
      content: ' ';
      position: absolute;
      top: 0px;
      left: 0px;
      right: 0px;
      transform: translate(0px, -50%);
      height: $timeline-border-width;
      background: $turquoise;
    }

    &:after {
      content: ' ';
      position: absolute;
      top: 0px;
      left: 0px;
      width: 0.875rem;
      height: 0.875rem;
      border-radius: 50%;
      transform: translate(-50%, -0.5rem);
    }

    &.left {
      padding-right: 6rem;
    }

    &.right {

      padding-left: 6rem;
      transform: translate(100%, 0px);

      &:after {
        left: auto;
        right: 0px;
        transform: translate(50%, -0.5rem);
      }

      .point-title {
        text-align: right;
      }

    }

    .point-title {
      position: absolute;
      top: -2.5rem;
      left: 2rem;
      right: 2rem;
      color: $black;
      font-size: 1.75rem;
      font-weight: bold;
      line-height: 1;
    }

    p {
      font-weight: bold;
      line-height: 1.4;
    }

  }

  .timeline-point-highlight {

    z-index: 1;
    position: relative;
    display: block;
    margin: -1.75rem auto 0px;
    padding: 2rem;
    width: 80%;
    border-style: solid;
    border-width: $timeline-border-width;
    border-color: $white;
    border-radius: 2rem;

    &.turquoise {
      background: $turquoise;
    }

    p {

      font-size: 1.375rem;

      &.highlight-title {
        margin-bottom: 1rem;
        font-size: 1.5rem;
        font-weight: bold;
        line-height: 1.5;
      }

      &:last-child {
        margin: 0px;
      }

    }

  }

  .timeline-section {

    position: relative;
    padding-bottom: 4rem;
    text-align: center;

    &:after {
      content: ' ';
      z-index: 0;
      position: absolute;
      top: 0px;
      bottom: 0px;
      left: 50%;
      transform: translate(-50%, 0px);
      width: $timeline-border-width;
    }

    &.turquoise {

      &:after {
        background: $turquoise;
      }

      .timeline-circle {
        background: $red;
        border-color: $turquoise;
      }

      .timeline-point {

        border-color: $turquoise;

        &:before,
        &:after {
          background: $turquoise;
        }

      }

    }

    &.red {

      &:after {
        background: $red;
      }

      h2.title-boxed {
        color: $red;
        background: $white;
        border-color: $red;
      }

      .timeline-circle {
        background: $yellow;
        border-color: $red;
      }

      .timeline-point {

        border-color: $red;

        &:before,
        &:after {
          background: $red;
        }

      }

    }

    &.white {

      color: $white;

      &:after {
        background: $white;
      }

      h2.title-boxed {
        color: $white;
        background: $red;
        border-color: $white;
      }

      .timeline-circle {
        background: $turquoise;
        border-color: $white;
      }

      .timeline-point {

        border-color: $white;

        &:before,
        &:after {
          background: $white;
        }

        .point-title {
          color: $white;
        }

      }

    }

  }

}

.case-other-link {

  padding: 6rem 2rem;
  display: block;
  color: $white;
  text-align: center;
  text-decoration: none;

  &.turquoise {

    background: $turquoise;
    transition: $transition;

    &:hover {
      background: $turquoise-dark;
    }

    h2.title-boxed {
      border-color: $white;
    }

  }

  h2.case-other-link-title {
    font-size: 2rem;
  }

}

.facts {

  padding: 4rem 0px;
  color: $white;
  background: $turquoise;

  .title-boxed {
    margin-top: 0px;
    border-color: $white;
  }

  .fact {
    font-size: 1.25rem;
    line-height: 1.4;
  }

}

footer {

  padding: 4rem 0px 1rem;
  color: $white;
  background: $turquoise-darkest;

  p:last-child {
    margin-bottom: 0px;
  }

  a {
    color: $white;
  }

  .social-list {

    list-style-type: none;
    margin: 0px 0px 1rem;
    padding: 0px;

    li {

      display: inline-block;
      vertical-align: middle;

      &:not(:first-child) {
        margin-left: 1rem;
      }

      img {
        display: block;
        width: 3rem;
      }

    }

  }

  .copyright {
    padding-top: 3rem;
    font-size: 0.875rem;
    line-height: 1.5;
    text-align: center;
  }

}

@media screen and (max-width: 1999px) {

  .home-header {
    height: 47rem;
  }

  .case-header .case-header-content {
    padding: 24rem 0px;
  }

}

@media screen and (max-width: 1849px) {

  .home-header {
    height: 44rem;
  }

}

@media screen and (max-width: 1699px) {

  .home-header {
    height: 41rem;
  }

  .case-header .case-header-content {
    padding: 19rem 0px;
  }

}

@media screen and (max-width: 1399px) {

  .home-header {
    height: 36rem;
  }

  .case-header .case-header-content {
    padding: 14rem 0px;
  }

}

@media screen and (max-width: 1099px) {

  .home-header {
    height: 30rem;
  }

  .case-header .case-header-content {
    padding: 8rem 0px;
  }

}

@media screen and (min-width: 768px) {

  .case-timeline {

    .timeline-circle,
    .title-boxed-wrap {

      opacity: 0;
      transition: $transition;

      &.forced-display {
        opacity: 1;
      }

      &.active {
        opacity: 1;
      }

    }

    .timeline-point-highlight {

      opacity: 0;
      transition: $transition;
      transform: scale(0.5, 0.5);

      &.turquoise {
        background: $red;
      }

      &.active {
        opacity: 1;
        transform: none;
      }

      &.color-fade {
        background: $turquoise;
      }


    }

    .timeline-circle:not(.timeline-circle-main-logo) {

      transform: scale(.5, .5);

      &.active {
        transform: none;
      }

    }

    .timeline-point {

      &,
      &:before,
      &:after {
        transition: $transition;
      }

      &:after {
        width: 0px;
        height: 0px;
      }

      &.right {

        &:before,
        &:after {
          right: 100%;
        }

      }

      &.left {

        &:before,
        &:after {
          left: 100%;
        }

      }

      .point-title,
      p {
        opacity: 0;
        transition: all 600ms;
        transition-delay: 600ms;
      }

      &.active {

        &:after {
          width: 0.875rem;
          height: 0.875rem;
        }

        &.right {

          &:before,
          &:after {
            right: 0%;
          }

        }

        &.left {

          &:before,
          &:after {
            left: 0%;
          }

        }

        .point-title,
        p {
          opacity: 1;
        }

      }

    }

  }

}

@media screen and (max-width: 1023px) {

  .grid-wrapper {
    width: 46rem;
  }

  .home-header {

    height: 26rem;

    .cvln-bw {
      height: 10rem;
    }

  }

  .home-preview {

    p {
      min-height: 0px;
    }

  }

  .preview-card {

    &-tight {

      margin-right: 2%;
      width: 49%;

      &:nth-child(2n) {
        margin-right: 0px;
      }

    }

    a {
      height: 300px;
    }

  }

  footer {

    .footer-address {
      margin-bottom: 2rem;
    }

  }

}

@media screen and (max-width: 959px) and (min-width: 768px) {

  .home-header {
    height: 22rem;
  }

  .break-md-only {
    display: block;
  }

}

@media screen and (max-width: 800px) and (min-width: 768px) {

  .case-timeline .timeline-point.active {

    &.left:before,
    &.left:after {
      left: 0.875rem;
    }

    &.right:before,
    &.right:after {
      right: 0.875rem;
    }

  }

}

@media screen and (max-width: 767px) {

  body {
    padding-top: 66px;
  }

  header {

    padding: 1rem 0px;

    .grid-wrapper {
      padding: 0px 1rem;
    }

    .logo-main {

      &.logo-full {
        display: none;
      }

      &.logo-cvln {

        display: block;

        img {
          height: 2rem;
        }

      }

    }

  }

  .main-menu-toggle {

    position: absolute;
    top: 50%;
    right: 1rem;
    transform: translate(0px, -50%);
    display: block;
    padding: 0.25rem;
    background: $red;
    border: 0px;
    outline: 0px;
    border-radius: 0.125rem;
    cursor: pointer;

    span {
      display: block;
      margin: 0.25rem 0px;
      width: 2rem;
      height: 0.25rem;
      background: $white;
      border-radius: 0.125rem;
    }

  }

  .nav-main-menu {

    position: fixed;
    z-index: 999;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    color: $white;
    background: transparentize($red, 0.05);
    text-align: center;

    a {

      color: $white;
      font-size: 2rem;

      &:before {
        content: '';
        display: none;
      }

    }

    ul {

      position: absolute;
      top: 50%;
      left: 0px;
      right: 0px;
      transform: translate(0px, -50%);
      padding: 0.5rem 1rem;

      li {

        display: block;

        &,
        &:not(:last-child) {
          margin-right: 0px;
        }

        a {
          display: block;
          padding: 1rem;
        }

      }

    }

  }

  .close-main-menu.open {
    position: fixed;
    z-index: 1000;
    top: 1rem;
    right: 1rem;
    display: block;
    color: $white;
    background: none;
    font-size: 2rem;
    line-height: 1;
    font-weight: bold;
    border: 0px;
    outline: 0px;
    cursor: pointer;
  }

  .home-header {

    height: 50vw;

    .cvln-bw {
      top: 50%;
      bottom: auto;
      transform: translate(-50%, -50%);
      width: 60%;
      height: auto;
    }

  }

  .page-header {

    height: 50vw;
    border-width: 2px;

    .title-boxed {
      font-size: 1.25rem;
    }

  }

  a.arrow-link {
    padding-right: 1.375em;
  }

  .nav-main-menu {
    display: none;
  }

  .nav-li-mobile {
    box-sizing: content-box;
  }

  .grid-wrapper {
    padding: 0px 2rem;
    width: auto;
  }

  .home-preview {
    margin-left: -2rem;
    margin-right: -2rem;
  }

  .preview-card {

    &-tight {

      &,
      &:nth-child(2n) {
        margin: 0px;
        width: 100%;
      }

    }

    .preview-card-title {
      width: auto;
      font-size: 1rem;
    }

    a {
      height: 250px;
    }

  }

  .case-preview:not(:last-child) {
    margin-bottom: 1.5rem;
  }

  .case-header {

    .accents {
      display: none;
    }

    .case-header-content {
      padding: 8rem 1rem;
    }

    .case-clearchoice & {
      background: {
        image: url('../images/cases/backgrounds/clearchoice.jpg');
        color: transparent;
        position: center center;
        size: cover;
      }
    }

    .case-ctca & {
      background: {
        image: url('../images/cases/backgrounds/ctca.jpg');
        position: center center;
        size: cover;
      }
    }

    video {
      display: none;
      width: 889px;
      height: 500px;
    }

  }

  .case-main-content {

    padding-top: 3rem;

    .title-boxed-wrap {
      margin: 0px;
    }

    h2.title-boxed {
      font-size: 1.25rem;
    }

  }

  .timeline-solid {

    &.timeline-red:after {
      display: none;
    }

  }

  .case-timeline {

    .title-boxed-wrap {
      margin-left: -1rem;
      text-align: left;
    }

    .timeline-section {

      text-align: left;

      &.timeline-section-first {

        &:after {
          top: 2rem;
        }

      }

      &:after {
        left: 0px;
      }

    }

    .timeline-circle {

      transform: translate(-50%, 50%);
      width: 3rem;
      height: 3rem;

      img {

        height: 2rem;

        &.tall {
          height: 1.5rem;
        }

        &.vtall {
          height: 1rem;
        }

      }

    }

    .timeline-point {

      margin-top: 0px;
      width: auto;

      &.left,
      &.right {

        margin: 0px;
        padding: 2rem;
        transform: none;

        .point-title {
          top: -1.75rem;
          left: 1rem;
          right: 1rem;
          font-size: 1.25rem;
          text-align: right;
        }

      }

      &:after {
        left: auto;
        right: 0px;
        transform: translate(50%, -0.5rem);
      }

    }

    .timeline-point-highlight {

      padding: 1.5rem;

      p {

        font-size: 1rem;

        &.highlight-title {
          font-size: 1.25rem;
        }

      }

    }

  }

  .home-map {

    .mobile-map-launch {
      display: block;
    }

    .google-map-wrap {

      display: none;

      &.open {

        display: block;
        z-index: 99999;
        position: fixed;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;

        .google-map {
          height: 100%;
        }

        .map-mobile-overlay {

          display: block;
          position: fixed;
          top: 0.5rem;
          right: 1rem;

          .map-close {
            padding: 0px;
            width: auto;
            color: $white;
            background: none;
            border: 0px;
            outline: 0px;
            font-weight: bold;
            font-size: 3rem;
            line-height: 1;
            text-shadow: 0px 0px 0.125em $black;
          }

        }

      }

    }

  }

  footer {
    padding-bottom: 3rem;
  }

}

@media screen and (max-width: 499px) {

  button {

    &.btn {
      display: block;
      width: 100%;
    }

  }

  .case-header {

    .case-header-content {
      padding: 4rem 1rem;
    }

  }

}
